/****************
 * Wrapper of setTimeout with given function.
 *
 *
 * */
export const delay = (action, timer) => {
    return new Promise((resolve, reject) => {
        setTimeout(function () {
            const r = action();
            resolve(r);
        }, timer);
    });
};
